/*Для экранов от 1200px*/
@media (min-width: 1200px) {
  .shop {
    border: 1px solid black;
    width: 60%;
    margin: 10px 20% 0px 20%;
  }
  h1 {
    text-align: center;
    background-color: orangered;
    color: white;
    padding: 5px 0px;
  }
  .card {
    border: 1px solid black;
    margin: 0px 10px 10px 10px;
  }
  .title {
    color: green;
    font-weight: bold;
    margin: 0px;
  }
  .vitrine {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .promo {
    padding: 5px;
  }
  input {
    margin-right: 5px;
  }
}

/*Для экранов до 480 px*/
@media (max-width: 480px) {
  .shop {
    border: 1px solid black;
    width: 96%;
    margin: 10px 2% 0px 2%;
  }
  h1 {
    text-align: center;
    background-color: orangered;
    color: white;
    padding: 2px 0px;
    font-size: 20px;
  }
  .card {
    border: 1px solid black;
    margin: 0px 10px 10px 10px;
    padding: 5px;
    font-size: 12px;
  }
  .title {
    color: green;
    font-weight: bold;
    font-size: 14px;
    margin: 0px;
  }
  .vitrine {
    display: block;
  }
  .promo {
    padding: 10px;
    display: flex;
    align-items: center;
  }
  input {
    margin-right: 5px;
    width: 40%;
    height: 14px;
  }
  button {
    font-size: 11px;
  }
}